@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css2?family=Muli:wght@200;300;400;500;600;700;800;900&display=swap);
 /*  https://colorlib.com/preview/theme/mighty/index.html  */

 *{
     margin: 0;
 }

 button:focus {
    outline: 0px dotted;
    outline: 0px auto -webkit-focus-ring-color;
}

 body{
    font-size: 1rem;
    color: #364d59;
    font-family: "Muli", sans-serif;
    font-weight:300;
    line-height:1.7;
 }


 a {
    color: #01d28e;
    -webkit-transition: .3s all ease;
    transition: .3s all ease;
}

a:hover {
    color: #000;
    text-decoration: none;
}

.bannerArea{
    position: relative;
    height: calc(100vh - 196px);
    min-height: 677px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    align-items: center;
    text-align: center;
}


.bannerArea:before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 1;
}

.bannerArea h1{
    position: relative;
    z-index: 1;
    font-size: 3rem;
    color: #fff;
    line-height: 1.5;
    font-weight: 300;
}

.bannerArea p{
    position: relative;
    z-index: 1;
    font-size:18px;
    color: #fff;
}

.bannerArea a{
    position: relative;
    z-index: 1;
    border-color: #fff;
    background: transparent;
    color: #fff;
    border-radius: 30px;
    padding:16px 48px;
    font-size: 1rem;
    font-weight: bold;
    line-height: 1.5;
}

.bannerArea a:hover{
    background: #fff;
}

.progress-wrap .progress{
    position: relative;
}

.progress-wrap .progress > div{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: green;
}

.progress-Rjs{
    width: 50%;
}

.progress-Js{
    width: 60%;
}

.progress-Bt{
    width:93%;
}

.progress-Jq{
    width: 80%;
}


.heading {
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 30px;
}

.heading:before, .heading:after {
    position: absolute;
    content: "";
    width: 50px;
    height: 2px;
    background: #000;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 0;
}

.heading:before {
    bottom: -6px;
    width: 80px;
}

.service {
    background-color: #fff;
    padding: 20px;
    -webkit-transition: .3s all ease;
    transition: .3s all ease;
}

.wrap-icon{
    font-size: 25px;
    font-weight: normal;
    padding-bottom: 20px;
}

.item-web{
    border-radius: 5px;
    overflow: hidden;
}

.item-web img{
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: .3s all ease;
    transition: .3s all ease;
}
.item-web:hover img{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.person-pic img {
    border-radius: 50%;
    max-width: 70px;
    background: #eee;
}
header{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index: 9;
    padding: 1rem;
}

.logo a{
    color: #fff;
    font-size: 1.7rem;
    font-weight: bold;
}

.menu-trigger{
    width: 32px;
    border: none;
    background: none;
    cursor: pointer;
}

.menu-trigger span{
    display: block;
    height: 3px;
    background: #fff;
    margin-top:4px;
}

header .navbar{
    position: fixed;
    right:-500px;
    top: 0;
    height:100%;
    background: #fff;
    width: 300px;
    text-align: left;
    padding: 70px 20px;
    -webkit-transition:all 500ms ease-in-out;
    transition:all 500ms ease-in-out;
}

.openMenu .navbar{
    display: block !important;
    right: 0;
}

.close-trigger{
    width: 24px;
    height: 24px;
    border: none;
    background: none;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 20px;
    z-index: 99;
}

.close-trigger span{
    display: block;
    height: 3px;
    background: #555;
    margin-top:4px;
    width: 100%;
    position: absolute;
    top: 20px;
    right: 15px;
}

.close-trigger span:first-child{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}

.close-trigger span:last-child{
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.navbar .navbar-nav .nav-item .nav-link{
    font-size: 20px;
    font-weight: 400;
    padding: 10px 20px;
    color: #212529;
}

.navbar .navbar-nav .nav-item .nav-link.active,
.navbar .navbar-nav .nav-item .nav-link:hover{
    color: #01d28e;
}
footer{
    background: #efefef;
    padding: 8em 0;
}

footer h4{
    font-size: 16px;
    color: #000;
    margin-bottom: 1.5rem
}

.footer-social a {
    display: inline-block;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background:rgba(255, 255, 255, 0.2);
    position: relative;
    margin: 0 2px;
}

.footer-social a:hover {
    background:#fff;
    color: #01d28e;
}

.footer-social a span {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

footer .form-control {
    border: 1px solid #e6e6e6;
    padding: 26px 12px;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
}

footer .btn-primary {
    color: #fff;
    background-color: #01d28e;
    border-color: #01d28e;
    border-radius: 30px;
    padding: 10px 20px;
    text-decoration: none;
    font-weight: bold;
}

footer .btn-primary:hover {
    color: #fff;
    background-color: #01ac74;
    border-color: #019f6c;
}
